.Header{
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90vw;
    padding: 0 5vw;
    height: 6rem;
    background-color: #181818;
    color: #fff;
}
.Logo{
    font-size: 2.2rem;
    font-weight: 500;
}
.Nav{
    display: flex;
    list-style-type: none;
}
.nav-links{
    margin-left:2rem;
    cursor: pointer;
    color: #fff;
    text-decoration: none;
    transition: 0.5s ease;
}
.nav-links:hover{
    transform: scale(1.1);
}

@media(max-width:600px){
    .Logo{
        font-size: 1.5rem;
    }
    .Nav{
        position: absolute;
        right: 0;
        top:6rem;
        flex-direction: column;
        justify-content: space-around;
        height: 40vh;
        width: 6rem;
        align-items: center;
        background-color: #181818;
        transition: 1s ease;   
    }
    .nav-links{
        margin-left: 0;
    }
    .ShowMenu{
        right: 0;
    }
    .HideMenu{
        right: -8rem;
    }

    .Hamburger{
        position: absolute;
        right: 3rem;
        top: 2rem;
        width: 2rem;
    }
    .line{
        width: 100%;
        height: 3px;
        background-color: #fff;
        margin-top:0.5rem;
    }

}